.header {
  width: 100%;
  position: fixed;
}

.header__container {
  height: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__list {
  list-style: none;
  display: flex;
}

@media only screen and (max-width: 499px) {
  .nav__list {
    flex-direction: column;
  }
}

@media only screen and (max-width: 499px) {
  .js-nav__list--collapsed {
    opacity: 0;
    z-index: -1;
  }
}

.nav__link {
  text-decoration: none;
  padding: 10px 20px;
}

.nav__hamburger {
  position: absolute;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  right: 25px;
  top: 25px;
  height: 60px;
  width: 70px;
  z-index: 3;
}

@media only screen and (min-width: 500px) {
  .nav__hamburger {
    display: none;
  }
}

.nav__hamburger span {
  background-color: #333;
  width: 60%;
  height: 4%;
  border-radius: 3px;
  transition: 0.3s transform;
}

.nav__hamburger span:first-child {
  margin-top: 16px;
}

.nav__hamburger span:last-child {
  margin-bottom: 14px;
}

.js-nav__hamburger--open span {
  transform: scale(0);
  transform-origin: center;
}

.js-nav__hamburger--open span:first-child {
  transform: translateY(9px) rotate(135deg);
}

.js-nav__hamburger--open span:last-child {
  transform: translateY(-19px) rotate(-135deg);
}

.main {
  min-height: calc(100vh - 150px);
}

.footer {
  min-height: 150px;
}

.notused {
  background-color: red;
}
